					{{item.text}}
					<view class="dian" v-if="item.active"></view>
				</view>
			</scroll-view>
			<scroll-view scroll-y="true" class="zhaor" :style="{height:`${height}upx`}">
				<view class="zhr dis_flex aic fs12 fw400">
					<text class="myC9">本地有</text>
					<text class="F43B31">{{num}}</text>
					<text class="myC9">名服务者为您服务</text>
				</view>
				<view class="zhaori">
					<view v-for="(item,index) in gnavlist" :key="index" @click.stop="gnav(index)">
						<view class="biao dis_flex aic flex-left F43B31 fw400">
							<view class="redLine"></view>
							<text class="fs12">{{item.type}}</text>
						</view>
						<view class="dis_flex" style="flex-wrap: wrap;" v-if="item.active">
							<view class="biao2 fs12" :class="{'F43B31':listitem.active,'biao1':listitem.active}"
								v-for="(listitem,lindex) in item.list" :key="lindex" @click.stop="gnlist(index,lindex)">
								{{listitem.text}}</view>
						</view>
					</view>
				</view>
			</scroll-view>
		</view>
	</view>

</template>

<script>
	import api from '../../components/service.js';
	import topbar from '@/components/top_bar/top_bar.vue'
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		components: {
			topbar
		},
		data() {
			return {
				height: 0,
				num: 20502,
				navlist: [{
						text: '工商财税',
						active: true
					},
					{
						text: '知识产权',
						active: false
					},
					{
						text: '建筑资质',
						active: false
					},
					{
						text: '许可证办理',
						active: false
					},
					{
						text: '行政审批',
						active: false
					},
					{
						text: '项目申报/认证',
						active: false
					},
					{
						text: '验资服务',
						active: false
					},
					{
						text: '审计评估',
						active: false
					}
				],
				gnavlist: [{
						type: '公司注册',
						active: true,
						list: [{
								text: '公司注册',
								active: true
							},
							{
								text: '企业名称疑难核准',
								active: false
							},
							{
								text: '外资中外合资企业注册',
								active: false
							},
							{
								text: '分公司注册',
								active: false
							},
							{
								text: '企业名称疑难核准',
								active: false
							}

						]
					},
					{
						type: '财税服务',
						active: false,
						list: []
					},
					{
						type: '其他服务',
						active: false,
						list: []
					},
					{
						type: '企业变更',
						active: false,
						list: []
					},
					{
						type: '企业注销',
						active: false,
						list: []
					}

				]
			}
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onReady() {
			this.height = uni.getSystemInfoSync().windowHeight * 2 - 90;
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			nav(index) {
				for (var i = 0; i < this.navlist.length; i++) {
					if (i == index) {
						this.navlist[i].active = true;
					} else {
						this.navlist[i].active = false;
					}
				}
			},
			gnav(index) {
				for (var i = 0; i < this.gnavlist.length; i++) {
					if (i == index) {
						this.gnavlist[i].active = true;
					} else {
						this.gnavlist[i].active = false;
					}
				}
			},
			gnlist(index, lindex) {
				console.log(index)

				for (var j = 0; j < this.gnavlist[index].list.length; j++) {
					if (j == lindex) {
						this.gnavlist[index].list[j].active = true;
					}else{
						this.gnavlist[index].list[j].active = false;
					}
				}
				uni.navigateTo({
					url:'/pagesA_lzc/fabu2/fabu2'
				})

			},
			back() {
				uni.navigateBack({
					delta: 1
				})
			}
		}
	}
</script>

